@import url('https://fonts.cdnfonts.com/css/dm-serif-text');

.journal-section {
    font-family: 'Times New Roman', Times, serif;
    background-color: #f9f9f9;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.journal-container {
    background-color: white;
    padding: 20px 40px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 1000px;
    width: 100%;
    margin: 20px;
}

.journal-title {
    font-family: 'DM Serif Text', serif;
    font-size: 2.5em;
    margin-bottom: 20px;
    text-align: start;
}

.journal-premise {
    font-size: 1.2em;
    margin-bottom: 20px;
    text-align: start;
}

.journal-author,
.journal-date {
    font-size: 1em;
    text-align: start;
    color: #999;
}

.journal-date {
    margin-bottom: 5px;
}

.journal-content {
    display: grid;
    grid-template-columns: 20% 80%;
    flex-wrap: wrap;
    justify-content: space-between; 
    align-items: center; 
    margin: 20px 0;
}

.content-section {
    flex: 1; 
    display: flex;
    flex-direction: column;
    align-items: center; 
    justify-content: center;
    height: 100%;
}

.content-section img {
    width: 100%; 
    max-width: 100%; 
    height: auto; 
    border-radius: 8px; 
}

.content-section p {
    color: rgba(0, 0, 0, 0.7); 
    margin-top: 10px;
}

.content-section-description {
    flex: 2; 
}

.content-section-description p {
    color: black;
}

.normal-content {
    display: block; 
    margin: 20px 0;
}

/* Additional styles for responsiveness */
@media (max-width: 768px) {
    .journal-content {
        display: grid; 
        grid-template-columns: 1fr;

    }

    .content-section {
        align-items: center; 
        text-align: center; 
    }

    .content-section-description {
        padding-left: 0;
        margin-top: 20px;
    }
}
